import { defineComponent, computed, ref } from 'vue';
import { getMenuLeafKeys } from '@/utils/map-menus';
import pageContent from '@/components/page-content';
import pageSearch from '@/components/page-search';
import pageModal from '@/components/page-modal';
import { searchFormConfig } from './config/search.config';
import { contentTableConfig } from './config/content.config';
import { modalConfig } from './config/modal.config';
import { usePageSearch } from '@/hooks/usePageSearch';
import { usePageModal } from '@/hooks/usePageModal';
import { useStore } from '@/store';
import { nextTick } from 'vue';
export default defineComponent({
  name: 'role',
  components: {
    pageContent,
    pageSearch,
    pageModal
  },
  setup() {
    const [pageContentRef, handleResetClick, handleQueryClick] = usePageSearch();
    //获取组件
    const elTreeRef = ref();
    //处理pageModal的hook
    const editCallBack = item => {
      const leafKeys = getMenuLeafKeys(item.menuList);
      //设置叶子节点
      nextTick(() => {
        elTreeRef.value?.setCheckedKeys(leafKeys);
      });
    };
    const [defaultInfo, pageModalRef, handleNewData, handleEditData] = usePageModal(undefined, editCallBack);
    const store = useStore();
    const menus = computed(() => store.state.allMenus);
    const otherInfo = ref({});
    const handleCheckChange = (data1, data2) => {
      const checkedKeys = data2.checkedKeys;
      const halfCheckedKeys = data2.halfCheckedKeys;
      const menuList = [...checkedKeys, ...halfCheckedKeys];
      otherInfo.value = {
        menuList
      }; //otherInfo接收的是对象, menuList是一个数组,所以要用{}包裹
    };

    return {
      contentTableConfig,
      searchFormConfig,
      modalConfig,
      handleResetClick,
      handleQueryClick,
      handleEditData,
      handleNewData,
      handleCheckChange,
      pageContentRef,
      pageModalRef,
      defaultInfo,
      otherInfo,
      menus,
      elTreeRef
    };
  }
});